              <div
                  class="elementor-element elementor-element-34bc84ba sectionHeading -left-line elementor-widget elementor-widget-highlighted-heading"
                  data-id="34bc84ba" data-element_type="widget" data-widget_type="highlighted-heading.default">
                <div class="elementor-widget-container">
                  <h2 class="highlighted-heading__title sectionHeading__title">با ما میخوای ارتباط بگیری ؟</h2></div>
              </div>
              <div
                  class="elementor-element elementor-element-214984ee text-sm text-black leading-4xl mt-48 md:mt-16 elementor-widget elementor-widget-text-editor"
                  data-id="214984ee" data-element_type="widget" data-widget_type="text-editor.default">
                <div class="elementor-widget-container">
                  <p>ت : {{ $store.state.contactUs.phone }}</p>
                  <p>م : {{ $store.state.contactUs.email }}</p>
                  <p></p>
                  <p>ن : {{ $store.state.contactUs.address }}</p>
                  </div>
              </div>
              <div
                  class="elementor-element elementor-element-46b6a740 sectionHeading__button mt-24 md:mt-20 elementor-widget elementor-widget-button"
                  data-id="46b6a740" data-element_type="widget" data-widget_type="button.button-hellix">
                <div class="elementor-widget-container">
                </div>
              </div>
              <div
                  class="elementor-element elementor-element-673bab0b socialsSection mt-24 elementor-shape-circle elementor-widget__width-inherit elementor-grid-0 e-grid-align-center elementor-widget elementor-widget-social-icons"
                  data-id="673bab0b" data-element_type="widget" data-widget_type="social-icons.default">
                <div class="elementor-widget-container">
                  <div class="footer__socials_content">
                    <a :href="'https://facebook.com/'+$store.state.contactUs.website_link" target="_blank">
                      <v-icon small color="#b88768" >mdi-facebook</v-icon> </a>
                    <a :href="'https://instagram.com/'+$store.state.contactUs.linkedin_link" target="_blank">
                      <v-icon small color="#b88768" >mdi-instagram</v-icon> </a>
                    <a :href="'https://t.me/'+$store.state.contactUs.tel_link" target="_blank">
                      <v-icon small color="#b88768" style="transform: rotate(-45deg)" >mdi-send-circle</v-icon> </a>
                  </div>
                </div>
              </div>

              <v-form ref="form" v-if="renderComponent">
              <v-text-field
                  v-model="name"
                  :rules="[$store.state.rules.required(name,'نام')]"
                  label="نام و نام خانوادگی"
              >

              </v-text-field>
              <v-text-field
                  v-model="email"
                  :rules="[$store.state.rules.email(email),$store.state.rules.required(email,'ایمیل')]"
                  label="ایمیل"
              >

              </v-text-field>
              <v-textarea
                  @keypress="isSpace($event)"
                  v-model="comment"
                  :rules="[$store.state.rules.required(comment,'پیغام')]"
                  label="پیغام شما"
              >

              </v-textarea>
                <div class="v-text-field__details mt-1 ml-3"><div class="v-messages theme--light"><div class="v-messages__wrapper"></div></div><div class="v-counter theme--light primary--text">{{spaces}} / 100</div></div>
              </v-form>

              <v-btn height="54" :disabled="loading" :loading="loading" elevation="0" @click="sendMessage()" class="rounded-0 mt-3 button-w-b hellix-elementor-button-text d-block mr-auto" style="min-width: 13.75rem;padding: 1.25rem 2rem;" color="primary" >ارسال پیام</v-btn>


            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>

import axios from "axios";
import {forceRerender} from "@/components/Misc/forceRerender";

export default {
  name: "ContactDetails",
  mixins : [forceRerender],
  data () {
    return {
      loading : false,
      name : null,
      email : null,
      comment : null,
      spaces : 0
    }
  },
  watch : {
    comment () {
      this.spaces = 0;
      for (let i in this.comment){
        if (this.comment.charAt(i) === " ") { // if a space is found in str
          this.spaces++; // add 1 to total so far
        }
        if(this.spaces>100){
          this.comment = this.comment.substring(0,i);
        }
      }
    }
  },
  methods : {
    isSpace: function(evt) {
      if(this.spaces===100){
        evt.preventDefault();
      }
    },
    sendMessage() {
      this.$refs.form.validate();
      if(this.$refs.form.validate()){
        this.loading = true;
        const name = this.name;
        const email = this.email;
        const comment = this.comment;
        this.name = null;
        this.email = null;
        this.comment = null;
        this.forceRerender();
        axios.post(this.$store.state.host + '/website/api/v1/website/create-message',{
          name,
          email,
          comment
        }).then(()=>{
          this.notify('پیغام شما دریافت شد','info');
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false;
        })
      }
    }
  }
}
</script>

